let Ajax = require("../util/Ajax.js")
export default{
	name:"pic",
	data(){
		return {
			isSmall:false,
			info:{}
		}
	},
	methods:{
		updataPic(){
			var that = this;
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					uni.showLoading({
						title: "上传中",
						mask: true
					})
					const tempFilePaths = res.tempFilePaths;
					console.log(tempFilePaths);

					for (var i = 0; i < tempFilePaths.length; i++) {
						uni.uploadFile({
							url: getApp().globalData.url+"album/updataPic", //仅为示例，非真实的接口地址
							filePath: tempFilePaths[i],
							name: 'file',
							formData: {
								'session': uni.getStorageSync("session"),
								'id':that.classId
							},
							success: (uploadFileRes) => {
								uni.hideLoading();
								var res=JSON.parse(uploadFileRes.data);
								if(res.code==1024){
									that.info.imgList.push(res.img);
								}
							}
						});
					}
					// if (this.imgList.length != 0) {
					// 	this.imgList = this.imgList.concat(res.tempFilePaths)
					// } else {
					// 	this.imgList = res.tempFilePaths
					// }
				}
			});
		},
		changeMode(){
			this.isSmall=!this.isSmall;
		},
		showImg(e){
			var index= e.currentTarget.dataset.index;
			wx.previewImage({
			  current: this.info.imgList[index], // 当前显示图片的http链接
			  urls: this.info.imgList // 需要预览的图片http链接列表
			})
		},
		//加载首页数据
		getAlbum(e){
			var that = this;
			Ajax.get({url:'album/getAlbumList',data:{
				id:e
			}})
			  .then((res)=>{//请求成功的回调函数
				res.data.imgList=res.data.imgList && res.data.imgList.split(",");
				that.info=res.data;
			  }).catch((res)=>{//捕获请求失败时的回调函数
			    console.log(res)
			  })
		}
	},
	mounted(){
		if(uni.getStorageSync("session")!="" && uni.getStorageSync("session")!=undefined && uni.getStorageSync("session").length>10){
			this.isLogin=true;
			this.isSmall=true;
		}else{
			this.isLogin=false;
			this.isSmall=false;
		}
	},
	onLoad(e) {
		this.getAlbum(e.id);
		this.classId=e.id;
	},
	onShareAppMessage(res){
		 if (res.from === 'button') {
		      // 来自页面内转发按钮
		      console.log(res.target)
		    }
		    return {
		      title: this.info.title,
		      path: '/pages/pic/pic?id='+this.info.Id
		    }
	
	}
}
